.header {
	font-size: 4em;
	font-family: 'Montserrat-Black';
	color: pink;
	margin: 0;
    overflow: visible;
   	background-image: -webkit-linear-gradient(
    	50deg,
        #e65353 16.666%, 
        #f2914b 16.666%, 
        #f2914b 33.333%, 
        #ffcb3b 33.333%, 
        #ffcb3b 50%, 
        #75AA67 50%, 
        #75AA67 66.666%, 
        #598bc9 66.666%, 
        #598bc9 83.333%, 
        #816799 83.333%);
   	background-image: -o-linear-gradient(
    	50deg,
        #e65353 16.666%, 
        #f2914b 16.666%, 
        #f2914b 33.333%, 
        #ffcb3b 33.333%, 
        #ffcb3b 50%, 
        #75AA67 50%, 
        #75AA67 66.666%, 
        #598bc9 66.666%, 
        #598bc9 83.333%, 
        #816799 83.333%);
   	background-image: linear-gradient(
    	40deg,
        #e65353 16.666%, 
        #f2914b 16.666%, 
        #f2914b 33.333%, 
        #ffcb3b 33.333%, 
        #ffcb3b 50%, 
        #75AA67 50%, 
        #75AA67 66.666%, 
        #598bc9 66.666%, 
        #598bc9 83.333%, 
        #816799 83.333%);
	background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
	-webkit-text-fill-color: transparent; 
	-webkit-animation: rainbow-text-simple-animation 2s; 
	        animation: rainbow-text-simple-animation 2s;
    -webkit-animation-delay: .25s;
            animation-delay: .25s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.header:hover {
    opacity: .85;
}

@-webkit-keyframes rainbow-text-simple-animation {
    0% {
        background-size: 100%;
    }
    50% {
        background-size: 200%;
    }
    100% {
        background-size: 100%;
    }
}

@keyframes rainbow-text-simple-animation {
    0% {
        background-size: 100%;
    }
    50% {
        background-size: 200%;
    }
    100% {
        background-size: 100%;
    }
}

@media (max-width: 780px) {
    .header {
        font-size: 2.5em;     
    }
    .header:hover {
        opacity: 1;
    }
}

@media (max-width: 490px) {
    .header {
        font-size: 1.5em;
    }
    .header:hover {
        opacity: 1;
    }
}






