body {
	height: 100%;
	width: 100%;
	min-width: 100%;
	min-height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	overflow: scroll;
}

.App {
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	font-family: 'Montserrat-Black';
	overflow: scroll;
}

@font-face {
	font-family: 'Montserrat-Black';
	src: local('Montserrat-Black'),
  	url('./Fonts/Montserrat-Black.ttf') format('truetype');
}






