.grid {
	width: 780px;
	height: 528px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	margin: 0;
	padding: 4px;
	padding-top: 14px;
	border-top: 4px solid #1c1c1c;
	border-left: 4px solid #1c1c1c;
	border-right: 4px solid #1c1c1c;
	border-bottom: 0px solid #1c1c1c;
	border-radius: 10px 10px 0px 0px;
	overflow: hidden;
	background: rgb(68,68,68);
	background: -webkit-radial-gradient(circle, rgba(68,68,68,1) 0%, rgba(27,27,27,1) 100%);
	background: -o-radial-gradient(circle, rgba(68,68,68,1) 0%, rgba(27,27,27,1) 100%);
	background: radial-gradient(circle, rgba(68,68,68,1) 0%, rgba(27,27,27,1) 100%);
	-webkit-box-shadow: 2px 0px 4px black;
	        box-shadow: 2px 0px 4px black;

}

@media (max-width: 780px) {
	.grid {
		width: 510px;
		height: 358px;
		padding-top: 3%;
	}
}

@media (max-width: 490px) {
	.grid {
		width: 400px;
		height: 285px;
	}
}

@media (max-width: 400px) {
	.grid {
		width: 350px;
		height: 283px;
		-webkit-box-shadow: none;
		        box-shadow: none;
		padding: none;
	}
}


